/* General Styling */

* {
    box-sizing: border-box;
}

body {
    font-family: "Karla", sans-serif;
       
}


/* Header Styling */

.header {
    display: flex;
    justify-content: center;
    height: 4rem;
    padding: 1.25rem;
    color: whitesmoke;
    background: rgb(131, 58, 180);
    background: linear-gradient(100deg, rgba(131, 58, 180, 1) 0%, rgba(253, 29, 29, 1) 50%, rgba(252, 176, 69, 1) 100%);
    align-items: center;
    width: 80%;
    border-radius: 1rem;
}

.header--img{
    height: 100%;
    margin-right: .5rem;
}

.header--title{
    font-size: 1.25rem;
    margin: auto;
}

.header--project{
    font-size: .75rem;
    font-weight: 500;
}

/* Form styling */

.form{
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap: 10px;
    height: 5rem;
    margin: 0 1rem;
      
}

.form--input {
    border-radius: .25rem;
    border: 1px solid #8160e3;
    margin: .75rem 0;
    padding: .25rem;
    height: 2.5rem;
    
}
.form--button{
    grid-column: 1/-1;
    color: white;
    background: rgb(218, 125, 12);
    border: none;
    border-radius: .25rem;
    font-weight: bold;
    cursor: pointer;      
    margin-top: 1rem; 
    height: 2.5rem;
    /* max-width: 100rem; */
}

/* Meme Image styling */

.meme{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.meme--section {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 80%;
    position: relative;
}

.meme--text{
     font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
     color:greenyellow;
     font-size:xx-large;
     text-align: center;
     text-shadow: 1px 1px 2px black;
     font-weight: bolder;
   
}
.meme--img{
    /* position: relative; */
    border-radius: 1rem;
    margin-top: 4rem;
    height: 75%;
}


.top{
    top: 5rem;
    position: absolute;
   
}

.bottom {
    bottom: 1rem;
    position: absolute;
}

.fullpage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}